@media only screen and (min-width: 960px) {
  .wp-block-group.constrain-content > .wp-block-group__inner-container {
    max-width: 1080px;
    margin-left: auto;
    margin-right: auto; } }

@media screen and (max-width: 768px) {
  .mobile-hidden {
    display: none; } }

.is-style-flexible-row .wp-block-group__inner-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center; }
  .is-style-flexible-row .wp-block-group__inner-container > * {
    padding-left: 0.6em;
    padding-right: 0.6em; }

.is-style-mega-title {
  text-transform: uppercase;
  font-weight: 800;
  letter-spacing: -0.0481em; }

h1.is-style-mega-title {
  font-size: 144px; }

h2.is-style-mega-title {
  font-size: 104px; }

h3.is-style-mega-title {
  font-size: 64px; }

h4.is-style-mega-title {
  font-size: 44px; }

h5.is-style-mega-title {
  font-size: 24px; }

h6.is-style-mega-title {
  font-size: 14px; }

@media (max-width: 575.98px) {
  h1.is-style-mega-title {
    font-size: 18vw; }
  h2.is-style-mega-title {
    font-size: 18vw; }
  h3.is-style-mega-title {
    font-size: 12vw; }
  h4.is-style-mega-title {
    font-size: 8vw; } }
